}
@media screen and (max-width:992px) {
    .custom_banner_heading {
        font-size: 30px;
    }
}

@media screen and (max-width:767px) {
    .custom_banner {
        text-align: center;
    }
    .custom_b_btns {
        justify-content: center;
    }
}
